@import url(https://use.fontawesome.com/releases/v5.7.2/css/all.css);
@import url(https://fonts.googleapis.com/css?family=Montserrat|Open+Sans:400,700);
header {
    background: #010005;
}

header .wrapper {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}

nav.main > ul {
    padding: 1rem 2rem;
    margin: 0;
    display: -webkit-flex;
    display: flex;
}

nav.main ul li {
    display: block;
    list-style: none outside;
    position: relative;
}

nav.main ul li a {
    display: block;
    color: #DDD;
    padding: 0.5rem 1rem;
    border-bottom: 2px solid transparent;
    border-top: 2px solid transparent;
    transition: 0.15s;
}

nav.main a:focus,
nav.main a:hover {
    color: white;
}

.site-branding {
    padding: 1.5rem;
}

.site-title {
    padding-bottom: 0;
}

.site-header .site-title a {
    display: block;
    text-decoration: none;
    color: #EEE;
    font-size: 2.5rem;
}

.site-header .site-description {
    font-family: 'Open Sans', sans-serif;
    font-weight: 200;
    letter-spacing: 0.2rem;
    text-transform: uppercase;
    color: #EEE;
    padding-bottom: 0;
}

.site-logo {
    width: 11rem;
}

#main-nav-toggle {
    display: none;
}

#toggle {
    position: relative;
    -webkit-flex: 0 0 auto;
            flex: 0 0 auto;
    -webkit-align-items: center;
            align-items: center;
    -webkit-align-content: center;
            align-content: center;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 1.5rem;
    color: #FFF;
    font-size: 1.5rem;
    cursor: pointer;
}

#times-icon {
    display: none;
}

@media screen and (max-width: 720px){
    nav.main {
        display: none;
    }

    nav.main ul li a {
        text-align: center;
        width: 100%;
    }

    nav.main ul li a:focus,
    nav.main ul li a:hover {
        color: #000;
        background: #fff;
    }

    nav.main ul {
        display: block;
        padding: 0;
    }

    nav.main ul li {
        padding: 0 0;
    }

    #main-nav-toggle:checked + .wrapper nav.main {
        display: block;
        -webkit-flex: 1 0 100%;
                flex: 1 0 100%;
    }

    #main-nav-toggle:checked + .wrapper .site-branding {
        display: block;
    }

    #main-nav-toggle:checked + .wrapper #toggle .fa-times {
        display: block;
    }

    #main-nav-toggle:checked + .wrapper #toggle .fa-bars {
        display: none;
    }
}

@media screen and (min-width: 720px){
    #toggle {
        display: none;
    }
}
.home-container {
    background: url(/brayn-cc/static/media/bg-2.40f40cba.png);
    background-size: cover;
    background-position: center;
    padding: 15rem 1rem;
    position: relative;
}

.home-container h2 {
    color: white;
    position: absolute;
    top: 0;
    right: 0;
    left: 0;
    text-align: center;
    font-weight: 400;
    font-family: sans-serif;
    text-transform: uppercase;
    letter-spacing: 0.55rem;
}

.home-credentials {
    color: white;
    padding: 3rem 1rem;
}

.text-wrapper {
    max-width: 60rem;
    margin: 0 auto;
    text-align: center;
}

.text-wrapper .home-credentials-text {
    font-family: Consolas, sans-serif;
    line-height: 2;
}

.text-wrapper a {
    /*DIB - otherwise scaling won't work*/
    display: inline-block;
    color: #222;
    padding: 0.75rem 3rem;
    background: white;
    margin: 0.75rem;
    border-radius: 30px;
    transition: 0.2s;
    -webkit-animation: pulse 1.5s infinite;
            animation: pulse 1.5s infinite;
}

.text-wrapper a:hover {
    -webkit-transform: scale(1.025);
            transform: scale(1.025);
}

@-webkit-keyframes pulse
{
    0% {
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.3);
    }
    50% {
        box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.3);
    }
    100% {
        box-shadow: 0 0 0 25px rgba(0, 0, 0, 0);
    }
}

@keyframes pulse
{
    0% {
        box-shadow: 0 0 0 0 rgba(255, 255, 255, 0.3);
    }
    50% {
        box-shadow: 0 0 0 0 rgba(255, 0, 0, 0.3);
    }
    100% {
        box-shadow: 0 0 0 25px rgba(0, 0, 0, 0);
    }
}

table.gridtable td {
    padding: 0.75rem;
    color: white;
}

table.gridtable td a {
    color: white;
    background: #FF4136 ;
    display: inline-block;
    padding: 0.5rem 1rem;
}

table.gridtable tr {
    background: #3a3a3a;
    border-bottom: 1px solid #4E5B6C;
}

/* Smartphone  */
@media only screen and (min-width : 220px)  and (max-width : 629px) {
    table tr {
        display: block;
        margin-top: 1rem;
    }

    table td {
        display: block;
        text-align: right;
        font-size: 13px;
        border-radius: 5px;
        margin-bottom: 1px;
        min-height: 14px;
        position: relative;
    }

    table td:last-child {
        padding-bottom: 2.5rem;
    }

    table td a {
        position: absolute;
        top: 0;
        right: 0.5rem;
    }

    table td:before {
        content: attr(data-name);
        float: left;
        font-weight: bold;
    }
}
.table-container {
    margin: 0 auto;
    width: 80%;
}

.table-container h2 {
    color: white;
    letter-spacing: 0.1rem;
    font-family: "Open Sans", sans-serif;
    font-weight: 400;
    text-align: center;
    text-transform: uppercase;
}

table.gridtable {
    margin: 0 auto;
    width: 95%;
    font-family: Helvetica, Arial, sans-serif;
    font-size: 14px;
    color: #333333;
    border-collapse: collapse;
    text-align: center;
}

table.gridtable th {
    padding: 0.75rem;
    background-color: #3a3a3a;
    color: white;
}

/* Large tablet  */
@media only screen and (min-width : 768px)  and (max-width : 1024px) {
    #container {
        width: 95%;
    }

    table.gridtable {
        width: 100%;
        font-size: 13px;
    }
}


/* Small tablet  */
@media only screen and (min-width : 630px)  and (max-width : 767px) {
    #container {
        width: 95%;
    }

    table.gridtable {
        width: 100%;
        font-size: 12px;
    }
}


/* Smartphone  */
@media only screen and (min-width : 220px)  and (max-width : 629px) {
    #container {
        width: 100%;
    }

    table.gridtable {
        width: 100%;
    }

    table thead {
        display: none;
    }
}
.details-container {
