    background: green;
    padding: 0.5rem;
    border: none;
    outline: none;
    cursor: pointer;
}

@media screen and (min-width: 500px) {
    .details-container article {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
    }

    .details-container article section {
        -webkit-flex: 1 1;
                flex: 1 1;
        text-align: left;
    }

    .details-container article:nth-of-type(even) {
        background: #3a3a3a;
    }

    .details-container article section:first-of-type {
        border-bottom-color: transparent;
    }
