section .invoice-item-container {
    display: block;
}

section .invoice-item-container section {
    padding: 0.3rem 0.75rem;
}
.site-footer {
    background: #010005;
    color: white;
    text-align: center;
    font-size: 0.9rem;
    padding: 1.5rem 0;
}

.site-footer a {
    text-decoration: none;
    color: lightcyan;
}

.dev-credits {
    text-shadow: 0 0 15px whitesmoke;
    text-decoration: none;
}
html {
    box-sizing: border-box;
}

*, *:after, *:before {
    box-sizing: inherit;
}

* {
    padding: 0;
    margin: 0;
}
html {
	font: 12px/1.5 "Montserrat", sans-serif;
    font-weight: bold;
}

body, form, table {
	font: inherit;
}

a {
	text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Open Sans", sans-serif;
    line-height: 1.2;
	margin: 0.75em 0 0.5em 0;
	padding: 0;
}

h1 { font-size: 2.2rem; }
h2 { font-size: 2.0rem; }
h3 { font-size: 1.8rem; }
h4 { font-size: 1.6rem; }
h5 { font-size: 1.4rem; }
h6 { font-size: 1.2rem; }

p {
	margin: 0;
	padding-bottom: 1.5rem;
}

ol, ul, dl {
	margin: 0 0 1.5rem 0;
}

ul, ol {
	padding: 0;
	margin-left: 1.5rem;
}

ul li, ol li {
	list-style-position: inside;
}

ul ul {
	margin-top: 0.5rem;
	margin-bottom: 0.5rem;
}

ul li { list-style-type: square; }
ul ul li { list-style-type: disc; }
ul ul ul li { list-style-type: circle; }

hr {
	margin: 2rem 0;
}

/* RESPONSIVE */

@media screen and (min-width: 500px){
    html {
        font-size: 14px;
    }
}

@media screen and (min-width: 800px){
    html {
        font-size: 16px;
    }
}

@media screen and (min-width: 1300px){
    html {
        font-size: 18px;
    }
}

@media screen and (min-width: 1800px){
    html {
        font-size: 21px;
    }
}
#infoBox, #errorBox {
    color: white;
    text-align: center;
    display: none;
    position: absolute;
    z-index: 1;
    padding: 0.5rem;
    width: 100%;
}

#infoBox {
    background: #393;
}

#errorBox {
    background: #F00;
}

#loadingBox {
    position: absolute;
    top: 3rem;
    display: none;
    width: 100%;
    text-align: center;
}

.lds-dual-ring {
    display: inline-block;
    width: 3rem;
    height: 3rem;
}
.lds-dual-ring:after {
    content: " ";
    display: block;
    width: 2.5rem;
    height: 2.5rem;
    margin: 1px;
    border-radius: 50%;
    border: 5px solid #fff;
    border-color: #fff transparent #fff transparent;
    -webkit-animation: lds-dual-ring 1.2s linear infinite;
            animation: lds-dual-ring 1.2s linear infinite;
}
@-webkit-keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}
@keyframes lds-dual-ring {
    0% {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    100% {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@media screen and (max-width: 720px) {
    #loadingBox {
        top: 6.75rem;
    }
}

