@import url('https://fonts.googleapis.com/css?family=Montserrat|Open+Sans:400,700');

html {
	font: 12px/1.5 "Montserrat", sans-serif;
    font-weight: bold;
}

body, form, table {
	font: inherit;
}

a {
	text-decoration: none;
}

h1, h2, h3, h4, h5, h6 {
	font-family: "Open Sans", sans-serif;
    line-height: 1.2;
	margin: 0.75em 0 0.5em 0;
	padding: 0;
}

h1 { font-size: 2.2rem; }
h2 { font-size: 2.0rem; }
h3 { font-size: 1.8rem; }
h4 { font-size: 1.6rem; }
h5 { font-size: 1.4rem; }
h6 { font-size: 1.2rem; }

p {
	margin: 0;
	padding-bottom: 1.5rem;
}

ol, ul, dl {
	margin: 0 0 1.5rem 0;
}

ul, ol {
	padding: 0;
	margin-left: 1.5rem;
}

ul li, ol li {
	list-style-position: inside;
}

ul ul {
	margin-top: 0.5rem;
	margin-bottom: 0.5rem;
}

ul li { list-style-type: square; }
ul ul li { list-style-type: disc; }
ul ul ul li { list-style-type: circle; }

hr {
	margin: 2rem 0;
}

/* RESPONSIVE */

@media screen and (min-width: 500px){
    html {
        font-size: 14px;
    }
}

@media screen and (min-width: 800px){
    html {
        font-size: 16px;
    }
}

@media screen and (min-width: 1300px){
    html {
        font-size: 18px;
    }
}

@media screen and (min-width: 1800px){
    html {
        font-size: 21px;
    }
}